#Nav_menu {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  
  .Nav_link:link {
    color: rgb(49, 49, 48);
    font-size: 1.6rem;
    line-height: 1.6rem;
    text-decoration: none;
  }
 .Nav_link{
     color: rgb(49, 49, 48);
 }
  .Nav_link:hover {
    color: rgb(49, 49, 48);
  }
  .Nav_link:active {
    color: rgb(49, 49, 48);
  }
  
  .activeRoute {
    
    border-bottom:0.1rem solid rgb(49, 49, 48);
    
  }